/*------> media queries <------*/

html {
  font-size: 62.5%;
}

@media screen and (min-width: 200px) and (max-width: 770px) {
  body {
    width: 100%;
  }
  .nav {
    width: 100%;
    justify-content: center;
  }
  .nav > div:nth-child(1) {
    font-size: 20px;
    width: 40%;
  }

  .nav > div:nth-child(1) h1 {
    font-size: 28px;
  }
  .nav > div:nth-child(2) {
    display: none;
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 100%;
  }
}

@media (max-width: 998px) {
  html {
    font-size: 85%;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 75%;
  }
}

@media (max-width: 600px) {
  html {
    font-size: 65%;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 320px) {
  html {
    font-size: 45%;
  }
}

@media (max-width: 280px) {
  html {
    font-size: 35%;
  }
}

@media (max-width: 240px) {
  html {
    font-size: 25%;
  }
}

@media screen and (min-width: 200px) and (max-width: 770px) {
    .nav > div:nth-child(1) {
      justify-content: left;
        width: 1rem;
        width: min-content;
      }

    .nav > div:nth-child(2) {
        margin-top: 14rem;
        width: 100%; 
        font-size: 1.2rem;
        justify-content: center;
    }

    .nav button {
        display: none; /* Hide the registration button */
    }
}


/*------> media queries <------*/

/* Navbar */

.nav {
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  width: 100%;
  height: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  position: fixed;
  top: 0;
  background-color: white;
}

.nav > div:nth-child(1) {
  width: 25%;
  padding-left: auto;
}

.nav > div:nth-child(2) {
  width: 45%;
  display: flex;
  justify-content: right;
  gap: 30px;
}

.nav > div:nth-child(2) > a {
  color: black;
  text-decoration: none;
}

.nav > div:nth-child(2) > a:hover {
  color: red;
  font-weight: bold;
}

.nav button {
  width: 60px;
  height: 60px;
  margin: .1rem;
  border: none;
  border-radius: 50%;
  color: white;
  font-family: "Poppins", sans-serif;
  background-color: black;
  font-size: 1rem;
  cursor: pointer;
}

.nav button:hover {
  background-color: rgb(180, 26, 169);
  color: rgb(8, 8, 8);
  border: solid 0.5px;
}

/* Stats */

.stats-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 30px;
  background-color: #f9f9f9;
}

.stats-card {
  width: 300px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 20px;
  margin: 10px;
}

.stats-card:hover{
  transform: scale(1.1);
  box-shadow: #343a40;
}

.stats-image {
  width: 100px;
  height: auto;
  margin-bottom: 20px;
}

.stats-value {
  font-size: 3.5rem;
  font-weight: bold;
  color: #080808;
}

.stats-value sup {
  font-size: 1.5rem;
  vertical-align: super;
}

.stats-divider {
  width: 50%;
  margin: 20px auto;
}

.stats-description {
  font-size: 1.5rem;
  color: #6c757d;
}


/* Footer */
.footer {
  background-color: #fffaf5;
  margin-top: 50px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.footer > div:nth-child(1) {
  display: flex;
  width: 90%;
  margin: auto;
  margin-bottom: 50px;

  justify-content: space-around;
}

.footer > div:nth-child(1) > div {
  width: 100%;
}

#line2 {
  width: 2px;
  background-color: rgb(28, 28, 28);
  margin-left: 70px;
  margin-right: 70px;
  transform: rotate(10deg);
}

.footer > div:nth-child(1) > div:nth-child(3) > div {
  display: flex;
  justify-content: space-between;
  border-bottom: solid 1px;
  border-color: lightgray;
}

.footer > div:nth-child(2) {
  display: flex;
  width: 90%;
  gap: 20px;
  margin: auto;
  border-top: solid 1px;
  border-color: lightgray;
  justify-content: space-between;
}

/*------> footer <------*/

/* Slider */

.slider-container {
  width: 100%;
  margin: auto;
  position: inherit;
}

.slick-slide {
  justify-content: center;
  align-items: center;
}

.slick-dots li button:before {
  color: gray;
}

.slick-dots li.slick-active button:before {
  color: black;
}

.slider-image {
  margin-left: 9rem;
  max-width: 100%;
  max-height: 300px;
}

/* About */

.about-container {
  max-width: 800px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

.about-header {
  text-align: center;
}

.founder-image {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.about-content {
  text-align: left;
}

.about-content h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.about-content p {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 20px;
}

/* Card Slider */

.slider {
  position: relative;
  width: 80%;
  margin: auto;
  overflow: hidden;
}

.slider-wrapper {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.card {
  min-width: 100%;
  transition: opacity 0.5s ease-in-out;
  opacity: 0;
}

.card.active {
  opacity: 1;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  cursor: pointer;
  padding: 1rem;
}

.arrow.left {
  left: 10px;
}

.arrow.right {
  right: 10px;
}

.card-content {
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  margin: 10px;
}

